@media screen and (min-width: 320px) {
  .ProductForm {
    font-family: Verdana;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .ProductSearch {
    display: flex;
    margin-top: 30px;
    padding-bottom: 10px;
    font-family: Verdana;
    font-style: normal;
    width: 290px;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.2;
    border: none;
    border-bottom: 1px solid #e0e0e0;
  }

  .ProductInput {
    display: flex;
    margin-top: 30px;
    padding-bottom: 10px;
    font-family: Verdana;
    font-style: normal;
    width: 290px;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.2;
    border: none;
    border-bottom: 1px solid #e0e0e0;
  }

  .ProductAddBtn::after {
    content: "Добавить";
  }

  .ProductAddBtn {
    margin: 60px auto;
    display: flex;
    justify-content: center;
    text-align: center;
    padding: 13px 46px;
    width: 176px;
    height: 44px;
    border: none;
    border-radius: 30px;
    font-size: 14px;
    font-weight: bold;
    color: white;
    background: #fc842d;
    box-shadow: 0px 4px 10px rgba(252, 132, 45, 0.5);
  }
}

@media screen and (min-width: 768px) {
  .ProductForm {
    display: flex;
    flex-direction: row;
    margin-bottom: 45px;
  }
  .ProductSearch {
    margin-top: 0px;
    padding: 0 0 20px 0;
    font-family: Verdana;
    width: 240px;
    margin-right: 30px;
  }

  .ProductInput {
    margin-top: 0px;
    padding: 0 0 20px 0;
    text-align: right;
    font-family: Verdana;
    width: 105px;
    margin-right: 50px;
  }

  .ProductAddBtn::after {
    content: "+";
  }

  .ProductAddBtn {
    display: inline;
    margin: 0;
    padding: 0;
    width: 50px;
    height: 50px;
    font-weight: 400;
    border: none;
    border-radius: 50%;
    font-size: 30px;
    color: white;
    background: #fc842d;
    box-shadow: 0px 4px 10px rgba(252, 132, 45, 0.5);
  }
}

@media screen and (min-width: 1280px) {
  .ProductInput {
    margin-right: 75px;
  }
}
