@import "../../styles/common.scss";
 
  .formInput {
    width: 290px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.04em;
    color: getColor("text-secondary");
    margin: 0;
    padding-top: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    border: none;
    border-bottom: 1px solid getColor("bg-secondary");
    &:focus {
      border-bottom: 1px solid getColor("accent");
      outline: none;
      &::placeholder {
          color: getColor("accent");   
    }
  }
    @include tablet {
      width: 240px;
    }
    @include desktop {
      width: 240px;  
    }

    &::placeholder {
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: 0.04em;
      color: getColor("text-secondary");       
    }
  }

